  margin-bottom: 5px;
}

.terminal {
  background-color: rgb(45, 9, 34);
  color: white;
  padding: 20px 30px;
  max-height: 500px;
  overflow-y: scroll;
}

.errorForm {
  color: #ff4a55;
  position: relative;
  top: -13px;
}

.table-action-logger {
  width: 800px;
}

.actionLogger {
  margin-right: 5px;
  margin-left: 5px;
}

.deleteMiddleware {
  text-decoration: underline;
  color: #dc3545 !important;
  cursor: pointer;
  margin-left: 5px;
}

.middleware {
  margin-top: 10px;
}

.middlwareInput {
  background-color: #ffffff;
  border-radius: 4px;
  color: #565656;
  padding: 8px 12px;
  box-shadow: none;
  height: 40px;
}

.spanLink {
  cursor: pointer;
  color: #3b5998;
  margin-right: 4px;
  margin-left: 4px;
}

.spanLink:hover {
  color: #1dc7ea;
}

.middleware-group {
  float: left;
  margin-right: 5px;
}

.modal-content {
  top: 40px;
}

.detailParams {
  margin-bottom: 50px;
}

.detailParams h4 {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 700;
}

.modal-run-spider .modal-body {
  padding: 0;
}

.paramSpider {
  margin-bottom: 5px;
}

.helpField {
  font-size: 12px;
  color: gray;
  font-style: italic;
}

.errorForm {
  margin-top: 5px;
  font-size: 30px;
}

/* .container-fluid {
  position: relative;
} */

.containerAuth .card {
  max-width: 630px;
  width: 100%;
  position: absolute;
  left: 49.1%;
  top: 49%;
  transform: translate(-50%, -70%);
  box-shadow: 0 0 7px rgba(0, 0, 0, 0.3);
}

.containerAuth h3 {
  margin-top: 0;
  margin-bottom: 20px;
}

.error-color {
  color: #ff4a55;
}
